<template>
    <div>
        <div>
            <label>아이디 <input type="text" v-model="user"></label>
        </div>
        <div>
            <label>패스워드<input type="password"></label>
        </div>
        <button @click = "loginUser" >로그인</button>
        <button @click = "$router.push('/')" >취소</button>
    </div>
  
</template>

<script>

export default {
    name : 'LoginView',
    data : ()=>({
        user : '',
    })
    ,
    methods : {
        loginUser : function () {
            this.$emit('login-user', this.user)
            this.$router.push('/');
        }
    }
}
</script>

